import { apiGetRelatedVideo } from '@/apis/index.js'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import MescrollUni from '@/components/mescroll-uni/mescroll-uni.vue'

export default {
	components: {
		uniNavBar,
		MescrollUni
	},
	data() {
		return {
			// 下拉刷新常用配置
			downOption: {
				auto: false
			},
			// 上拉加载常用配置
			upOption: {
				auto: false,
				page: {
					num: 0,
					size: 10
				},
			},
			curNav: 1,
			navList: [
				{ name: '广场', id: 3452 },
				{ name: '动态', id: 3462 },
			],
			dateMsg: {
				curDate: [],
				msgList: [
					{ name: '他是我见过最年少有为的人', id: '' },
					{ name: '因为一个人而失眠，这真的好吗', id: '' },
					{ name: '花开花落，没有悲愁怎么是少年', id: '' },
				]
			},
			allList: [],
			showList: []
		};
	},
	onLoad() {
		let date = new Date()
		this.dateMsg.curDate = String(date).split(' ')
		this.switchTab(3245, 1)
	},
	methods: {
		// 标题栏切换
		switchTab (id, index) {
			this.curNav = index
			this.getList(1, 10, res => {
				this.allList = res
				this.formatList()
			})
		},
		// 获取相关视频
		getList(pageNum, pageSize, successCallback, errorCallback) {
			const params = {
				id: this.curNav + 1,
				pageNum,
				pageSize
			}
			apiGetRelatedVideo(params).then(res => {
				// 联网成功回调
				successCallback && successCallback(res.data)
			}, res => {
				// 联网失败回调
				errorCallback && errorCallback()
			})
		},
		// 下拉刷新回调
		downCallback(mescroll) {
			mescroll.resetUpScroll()
		},
		// 上拉加载回调
		upCallback(mescroll) {
			// 联网加载数据
			this.getList(mescroll.num, mescroll.size, res => {
				// 设置列表数据
				if (mescroll.num == 1) this.allList = []
				if (mescroll.num == 1) res = [...res, ...res] // 第一页数据量太少
				this.allList = this.allList.concat(res)
				
				this.formatList()
				mescroll.endSuccess()
			}, () => {
				// 联网失败的回调，隐藏下拉刷新的状态
				mescroll.endErr();
			})
		},
		formatList () {
			let i = 0,
				colList1 = [],
				colList2 = [],
				list = this.allList;
			
			while (i < list.length) {
				colList1.push(list[i++]);
				if (i < list.length) {
					colList2.push(list[i++]);
				}
			}
			
			this.showList = {
				colList1,
				colList2,
			}
		},
		goCloud() {
			uni.showToast({
				icon: 'none',
				title: '功能未开发'
			})
		}
	}
}
